.dropdown-menu {
  position: relative;
  z-index: 1000;

  .menu {
    min-width: 200px;
    position: absolute;
    top: 100%;
    // right: 0;
    left: 0;
    border-radius: 4px;
    background-color: white;
    padding: 8px 0;
    box-shadow: rgb(0 0 0 / 20%) 0px 5px 5px -3px, rgb(0 0 0 / 14%) 0px 8px 10px 1px, rgb(0 0 0 / 12%) 0px 3px 14px 2px;
  }
}
