.text-field {
  position: relative;

  &.fullWidth {
    width: 100%;

    input {
      width: 100%;
    }
  }

  & input {
    padding: 12px 16px;
    border-radius: 8px;
    border-color: #5285cc;
    border-style: solid;
  }

  .label {
    font-size: 10px;
    position: absolute;
    top: -5px;
    left: 15px;
    background-color: white;
    padding: 0 5px;
  }
}
